<template>
    <div class="chart">
        <div
            v-if="chart && type == 'pie'"
            class="chart__legend"
        >
            <div
                v-for="data in this.chart.series[0].data"
                class="chart__legend-element"
                :class="{ 'chart__legend-element_inactive': data.state == 'inactive' }"

                @mouseover="onLabelHover(data)"
                @mouseout="onLabelOut"
            >
                <div
                    class="chart__legend-name"
                >
                    <div
                        class="chart__legend-color"
                        :class="`chart__legend-color_color_${data.colorIndex}`"
                    ></div>
                    {{ data.name }}
                </div>
                <div class="chart__legend-value">
                    {{ data.percentage | formatPercent }}
                </div>
            </div>
        </div>
        <div
            :id="id"
            class="chart__container"
            :class="`chart__container_type_${type}`"
        ></div>
    </div>
</template>

<script>
let highcharts;

export default {
    name: 'ElementChart',

    props: {
        data: Object,
        type: String,
    },

    data() {
        return {
            chart: null,
        };
    },

    computed: {
        id() {
            return `chart-${this.$getRandomString()}`;
        },
    },

    mounted() {
        import(/* webpackChunkName: "highcharts" */ 'highcharts').then((lib) => {
            highcharts = lib.default;
            highcharts.setOptions({
                chart: {
                    styledMode: true,
                    useHTML: true,
                },
                tooltip: {
                    useHTML: true,
                    distance: 5,
                },
                credits: {
                    enabled: false,
                },
                title: {
                    text: '',
                },
            });

            this.chart = highcharts.chart(this.id, this.data);
        });
    },

    methods: {
        onLabelHover(current) {
            this.chart.series[0].data.forEach((data) => this.updateState(data, 'inactive'));
            current.setState('hover');
        },

        onLabelOut() {
            this.chart.series[0].data.forEach((data) => this.updateState(data));
        },

        updateState(data, state = null) {
            // Вызываем все реактивные штуки, а то они почему-то так не вызываются
            data.setState(state);
            const { percentage } = data;

            data.percentage = null;
            data.percentage = percentage;
        },
    },
};
</script>

