<script>
import ElementChart from '../components/ElementChart';
import DropdownSelectable from '../components/DropdownSelectable';

export default {
    name: 'MainStats',
    components: {
        DropdownSelectable,
        ElementChart,
        ElementPlaceholder: () => import('../components/ElementPlaceholder'),
    },

    data() {
        return {
            popularityData: [{ y: 0.2, name: '\u0414\u043e\u043d\u0430\u0442 \u043a\u0435\u0439\u0441' }, { y: 0.12, name: '[\u0422] \u0410\u043d\u0438\u043c\u0435 \u043a\u0435\u0439\u0441' }, { y: 0.19, name: '[\u0422] VERT \u043a\u0435\u0439\u0441' }, { y: 0.05, name: '[\u041f] SCP \u043a\u0435\u0439\u0441' }, { y: 0.17, name: '\u0422\u0435\u043d\u044c' }, { y: 0.03, name: '[\u0422] \u041c\u0443\u043b\u044c\u0442\u0444\u0438\u043b\u044c\u043c \u043a\u0435\u0439\u0441' }, { y: 0.08, name: '[\u041f] \u041f\u0432\u041f 2 \u043f\u0440\u0435\u0444\u0438\u043a\u0441\u044b' }, { y: 0.02, name: '[\u041f] \u0418\u0433\u0440\u043e\u0432\u043e\u0439 \u043a\u0435\u0439\u0441' }, { y: 0.01, name: '\u0414\u0438\u043a\u0442\u0430\u0442\u043e\u0440' }, { y: 0.03, name: '\u0412\u043e\u0436\u0434\u044c' }, { y: 0.03, name: '\u0412\u043e\u0441\u0441\u0442\u0430\u043d\u043e\u0432\u043b\u0435\u043d\u0438\u0435 \u041f\u0430\u0440\u043e\u043b\u044f' }, { y: 0.03, name: '[\u0422] \u041e\u0431\u044a\u0435\u0434\u0438\u043d\u0451\u043d\u044b\u0439 \u043a\u0435\u0439\u0441' }, { y: 0.03, name: '[\u041f] \u041f\u0432\u041f \u043f\u0440\u0435\u0444\u0438\u043a\u0441\u044b' }, { y: 0, name: '\u0411\u043e\u0435\u0446' }],
        };
    },

    methods: {
        tooltipFormatter(data) {
            const [date, month] = data.x.split('.');
            const day = capitalizeFirstLetter(this.$moment().month(month).date(date).format('dddd, DD MMMM'));
            const sponsors = data.points[0].y;
            const income = this.$format.money(data.points[1].y);

            return `
                    ${day}:<br/>
                    Новые спонсоры — ${sponsors}<br/>
                    Прибыль — ${income}<br/>
                `;

            function capitalizeFirstLetter(string) {
                return string.charAt(0).toUpperCase() + string.slice(1);
            }
        },
    },
};
</script>

